import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';

class Square extends React.Component {
    render(){
        return(
            <button 
                className='square' 
                onClick={this.props.onClick}
            >
                
                {this.props.value}

            </button>

        );
    } 
}

class Board extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            square: Array(9).fill(null),
            isNext: true,
        };
    }
    
    handleClick(i) {
        if (this.state.square[i] === null) {
            const squareCopy = this.state.square.slice();
            squareCopy[i] = this.state.isNext ? 'x' : 'o';
            this.setState({
                square: squareCopy,
                isNext: !this.state.isNext
            });
        }
    }

    renderSquare(i){
        return(
            <Square 
                value = {this.state.square[i]}
                onClick={() => this.handleClick(i)}
            />     
        );

    };

    render(){
        return(
            <div className='board'>
                <div className='board-row'>
                    {this.renderSquare(1)}
                    {this.renderSquare(2)}
                    {this.renderSquare(3)}
                </div>
                <div className='board-row'>
                    {this.renderSquare(4)}
                    {this.renderSquare(5)}
                    {this.renderSquare(6)}
                </div>
                <div className='board-row'>
                    {this.renderSquare(7)}
                    {this.renderSquare(8)}
                    {this.renderSquare(9)}
                </div>

            </div>

        );
    } 
};

class Game extends React.Component {
    render(){
        return (
            <div className = 'game'>
                <p className = 'game-name'>React Tic Tac Toe</p>
                <div className = 'game-board'>
                    <Board />

                </div>
            </div>
        )
    }
}

ReactDOM.render(
    <Game/>,
    document.getElementById('root')
);
